export const dateFormater = (date) => {
    let arr = []
    const value = date.toLocaleDateString().replace(/[./]/g, "-");
    arr.push(value.slice(6, 10))
    arr.push(value.slice(3, 5))
    arr.push(value.slice(0, 2))
    return arr.join("-")
}

